@import url('https://fonts.googleapis.com/css?family=Montserrat|News+Cycle|Source+Sans+Pro');

body{
    margin: 0;
}

.background{
    background-color: #344C84;
    min-height: 100vh;
    display: flex;
    flex-direction: column;

}

.header{
    width: 100%;
    font-family: 'Source Sans Pro', sans-serif;
    display: flex;
    flex-direction: row;
    padding: 30px;
    justify-content: space-between;
    box-sizing: border-box;
    color: #F8F7F7;

    .logo{
        padding-left: 20px;
        font-size: 35px;
    }
}

.nav{
    display: flex;
    flex-direction: row;

    ul{
        list-style-type: none;
        display: flex;
        flex-direction: row;
        padding-left: 0px;
        padding-right: 50px;

        li{
            padding-left: 5px;
            padding-right: 5px;
        }

    }
}

.landing-body{

    display:flex;
    flex-direction: column;
    justify-content: flex-start;
    align-content: center;
    

    .landing-header-container{
        background-color: #9CACC3;

        display: flex;
        flex-direction: row;
        justify-content: center;
        margin-right: auto;
        margin-left: auto;
        border-radius: 2px;
        box-shadow: 0 12px 24px rgba(0,0,0,0.2);
    }

    .landing-header{
        font-family: 'News Cycle', sans-serif;
        font-size: 30px;
        text-transform: lowercase;
        padding: 5px 10px 5px 10px;
    }

    .landing-cards-container{
        margin: 100px 30px 0px 30px;
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: space-evenly;

        .landing-card{
            background-color: #76AED2;
            height: 450px;
            width: 350px;
            font-family: 'Source Sans Pro', sans-serif;
            display: flex;
            flex-direction: column;
            // text-align: center;
            // border: 2px solid #A6AEAF;
            border-radius: 5px;

            .card-content{
                display: flex;
                flex-direction: column;
                justify-content: space-between;
                align-items: center;
                margin: 15px 0px 0px 0px;
                flex-grow: 1;
                // height: 100%;
                .card-icon{
                    // background-color: #876E90;
                    // width: 140px;
                    // height: 140px;
                    // display: flex;
                    // flex-direction: row;
                    // justify-content: center;
                    // align-content: center;
                    margin-bottom: 15px;

                    img{
                        width: 130px;
                        height: 130px;
                    }
                }

                .card-text-content{

                    display: flex;
                    flex: 1 1 auto;
                    flex-direction: column;
                    justify-content: space-around;
                    background: #F8F7F7;
                    margin: 0px 35px 20px 35px;
                    border-radius: 5px;


                    .card-header{
                        // text-align: center;
                        // margin-top: 15px;
                        font-size: 26px;
                        margin: 0px 20px 0px 20px;
                        text-align: center;
                    }
    
                    .card-text{
                        // margin-top: 10px;
                        font-size: 14px;
                        margin: 0px 20px 10px 20px;
                    }

                }


                .card-link{
                    padding: 10px 0px 10px 0px;
                    width: 100%;
                    background-color: #A6AEAF;
                    border-bottom-left-radius: 5px;
                    border-bottom-right-radius: 5px;
                    text-align: center;
                    border-top: 2px solid #344C84;
                }
            }
        }
    }   
}